
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import Global from "../utils/global";
import wxShare from '../utils/wxshare';
export default {
name: 'Show',
components: {
Alert,
XDialog
},
data () {
return {
now: '',
invitor: this.$route.query.invitor,
shareUrl: '',
wxid : this.$route.query.wxid
}
},
created(){
let self = this;
self.getNowTime();
self.go();
self.getWxShare();
},
methods:{

getWxShare(){
let self = this;
self.$http.post(global.baseUrl+global.url.get_wx_share,{}).then(res => {
this.shareUrl = res.data
var shareUrl = this.shareUrl+this.wxid;
wxShare({ title: '111', desc: '222', link: shareUrl , logo: ''});
});
},

modalbox(){
this.$refs.alertBox.style.display = 'block';
},

toggles(){
this.$refs.alertBox.style.display = 'none';
},

getNowTime(){
var myDate = new Date();
var _minutes = '';

if(myDate.getMinutes() < 10){
_minutes = "0" + myDate.getMinutes();
}else{
_minutes = myDate.getMinutes();
}
var _time = myDate.getHours()+":"+ _minutes;
this.now = _time;
},

go(){
let self = this;
var _i = 1;
var s = setInterval(() => {
switch (_i) {
case 1:
var el = self.$refs.go1;
if(el){
el.style.display = 'block';
}
break;
case 2:
var el = self.$refs.go2;
if(el){
el.style.display = 'block';
}
break;
case 3:
var el = self.$refs.go3;
if(el){
el.style.display = 'block';
}
break;
case 4:
var el = self.$refs.go4;
if(el){
el.style.display = 'block';
}
break;
case 5:
var el = self.$refs.go5;
if(el){
el.style.display = 'block';
}
break;
case 6:
var el = self.$refs.go6;
if(el){
el.style.display = 'block';
}
break;
case 7:
var el = self.$refs.go7;
if(el){
el.style.display = 'block';
}
break;
case 8:
var el = self.$refs.go8;
if(el){
el.style.display = 'block';
}
break;
case 9:
clearInterval(s);
break;
}
_i ++;

var ele = document.getElementById('show');
var _a = ele.scrollHeight;
ele.scrollTop =  _a;

},800)
},
jumpFun(){
window.location.href = 'http://www.baidu.com';
}
},
mounted(){



let self = this,
_url = window.location.href;

pushHistory();

window.addEventListener("popstate", function(e) {
self.jumpFun();
}, false);

function pushHistory() {
var state = {
title: "title",
url: _url
};
window.history.pushState(state, "title", _url);
}



window.document.oncontextmenu = function (e) {
e.preventDefault();
};

var startX = 0, startY = 0;

function touchStart(e) {
try {
var touch = e.touches[0],
x = Number(touch.pageX),
y = Number(touch.pageY);
startX = x;
startY = y;
} catch (e) {
alert(e);
}
}

document.addEventListener('touchstart', touchStart);
var ele = document.getElementById('show');
ele.ontouchmove = function (e) {
var point = e.touches[0],
eleTop = ele.scrollTop,
eleScrollHeight = ele.scrollHeight,
eleOffsetHeight = ele.offsetHeight,
eleTouchBottom = eleScrollHeight - eleOffsetHeight;
if (eleTop === 0) {
if (point.clientY > startY) {
e.preventDefault();
}
}
else if (eleTop === eleTouchBottom) {
if (point.clientY < startY) {
e.preventDefault()
}
}
};

}
}



