
import wxShare from '../utils/wxshare';
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name: 'Process',
components: {
Alert,
XDialog
},
data () {
return {
step: this.$route.query.step,
wxid: this.$route.query.wxid,
words:'',
}
},
created() {
let self = this;
self.$http.post(global.baseUrl+global.url.get_wx_share,{}).then(res => {
this.shareUrl = res.data
var shareUrl = this.shareUrl+this.wxid;
wxShare({ title: '444', desc: '555', link: shareUrl , logo: ''});
});
self.getAlertBox();
},
watch: {
'$route' (to, from) {
this.step = this.$route.query.step;
}
},
methods:{
jumpFun(){
window.location.href = 'http://www.baidu.com';
},

modalbox(){
this.$refs.alertBox.style.display = 'block';
},

toggles(){
this.$refs.alertBox.style.display = 'none';
},

getAlertBox(){
let self = this,
step = self.step,
_str = "";
step = parseInt(step);
switch (step) {
case 1:
_str = '<div class="font-one">分享成功</div><div class="font-two">请继续分享<i class="ui-color-red"> 1 </i>个不同的群</div><div class="font-three"><i class="ui-color-red">即可进群</i></div>';
break;
case 2:
_str = '<div class="font-one">分享失败</div><div class="font-two">分享到相同的群或者个人会失败<br>请继续分享到<i class="ui-color-red"> 2 </i>个不同的群！</div><div class="font-three"><i class="ui-color-red">即可进群</i></div>';
break;
case 3:
_str = '<div class="font-one">分享成功</div><div class="font-two">请继续分享到<i class="ui-color-red"> 1 </i>个不同的群！</div><div class="font-three"><i class="ui-color-red">即可进群</i></div>';
break;
case 4:
_str = '<div class="font-one"><i class="ui-color-red">分享完成</i></div><div class="font-four">请分享到<i class="ui-color-red"> 朋友圈 </i>即可进群！</div>';
break;
case 5:
_str = '<div class="font-six">由于参与人数过多！<br>群主稍后拉你进群，请耐心等待</div><div class="font-seven">朋友圈信息不可删除 <br><i>否则无法核实！</i></div>';
break;
}
this.words = _str;
}

},
mounted(){

let self = this,
_url = window.location.href;

pushHistory();
window.addEventListener("popstate", function(e) {
self.jumpFun();
}, false);

function pushHistory() {
var state = {
title: "title",
url: _url
};
window.history.pushState(state, "title", _url);
}



window.document.oncontextmenu = function (e) {
e.preventDefault();
};


var startX = 0, startY = 0;
function touchStart(e) {
try {
var touch = e.touches[0],
x = Number(touch.pageX),
y = Number(touch.pageY);
startX = x;
startY = y;
} catch (e) {
alert(e);
}
}

document.addEventListener('touchstart', touchStart);
var ele = document.getElementById('Process');
ele.ontouchmove = function (e) {
var point = e.touches[0],
eleTop = ele.scrollTop,
eleScrollHeight = ele.scrollHeight,
eleOffsetHeight = ele.offsetHeight,
eleTouchBottom = eleScrollHeight - eleOffsetHeight;
if (eleTop === 0) {
if (point.clientY > startY) {
e.preventDefault();
}
}
else if (eleTop === eleTouchBottom) {
if (point.clientY < startY) {
e.preventDefault()
}
}
};

var _h = document.documentElement.clientHeight;
document.getElementById('Process').style.height = _h + 'px';

}
}



