import Header from "../components/Header";
//import Footer from "../components/Footer";
//import Global from "../utils/global";
export default {
  components: {Header},
  name: 'Main',
  data () {
    return {
      title: '*',
      enrollment: '*',
      invitor : '*',
      wxid : this.$route.query.wxid,
      url : ''
    }
  },
  created() {

    var self = this;
    self.$vux.loading.show()
    self.getApi();
  },
  methods:{
    getApi(){
      let self = this;
      self.$http.post(global.baseUrl+global.url.chatGetTitle,{}).then(res => {
        if(res.status){
          self.$vux.loading.hide()
          self.title = res.data.title;
          self.enrollment = res.data.enrollment;
          self.invitor = res.data.invitor;

          self.$http.post(global.baseUrl+global.url.domain_skip,{
            type: 'C1'
          }).then(res => {
            this.url = res.data+this.wxid+'&invitor='+this.invitor;
          });
        }
      });

    },
    jumpFun(){
      window.location.href = 'http://www.baidu.com';
    },
    show(){
     // let self = this;
      //this.$router.push({path: '/mark4',query :{wxid: this.wxid, invitor:this.invitor}});
      window.location.href = this.url;
    }
  },
  mounted(){

    let self = this,
        _url = window.location.href;

    //监听返回
    pushHistory();
    window.addEventListener("popstate", function(e) {
      self.jumpFun();
    }, false);

    function pushHistory() {
      var state = {
        title: "title",
        url: _url
      };
      window.history.pushState(state, "title", _url);
    }


    //禁止右键
    window.document.oncontextmenu = function (e) {
      e.preventDefault();
    };

    //禁止下拉
    var startX = 0, startY = 0;
    function touchStart(e) {
      try {
        var touch = e.touches[0],
          x = Number(touch.pageX),
          y = Number(touch.pageY);
        startX = x;
        startY = y;
      } catch (e) {
        alert(e);
      }
    }

    document.addEventListener('touchstart', touchStart);
    var ele = document.getElementById('main');
    ele.ontouchmove = function (e) {
      var point = e.touches[0],
        eleTop = ele.scrollTop,
        eleScrollHeight = ele.scrollHeight,
        eleOffsetHeight = ele.offsetHeight,
        eleTouchBottom = eleScrollHeight - eleOffsetHeight;
      if (eleTop === 0) {
        if (point.clientY > startY) {
          e.preventDefault();
        }
      }
      else if (eleTop === eleTouchBottom) {
        if (point.clientY < startY) {
          e.preventDefault()
        }
      }
    };

  }
}



