

import Vue from 'vue'
import router from '../router/index'
/*import Global from './global'*/


import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'


Vue.use(WechatPlugin)
Vue.use(AjaxPlugin)


export default function wxShare ({title, desc, timelineTitle, link, imgUrl} = {}) {

var _url = window.location.href,
step = router.history.current.query.step,
wxid = router.history.current.query.wxid;
if(_url && wxid ){
Vue.http.post(global.wxUrl+global.url.wx_share, {
url : _url,
wxid : wxid
}).then(res => {
Vue.wechat.config({
debug: false,
appId: res.data.appId,
timestamp: res.data.timestamp,
nonceStr: res.data.nonceStr,
signature: res.data.signature,
jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline']
})
});
}

Vue.wechat.ready(() => {
Vue.wechat.onMenuShareAppMessage({
title: title, // 分享标题
desc: desc || '这里是分享朋友的内容', // 分享描述
link: link || window.location.href, // 分享链接
imgUrl: imgUrl || 'https://dwz.cn/T2afCN3o', // 分享图标
success: function () {
if(step){
step = parseInt(step);
}
switch (step) {
case 0:
case 1:
step = 2;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 2:
step = 3;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 3:
step = 4;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 4:
step = 5;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 5:
step = 6;
window.location.href = 'http://www.baidu.com';
break;
default:
step = 1;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
}
},
})
Vue.wechat.onMenuShareTimeline({
title: timelineTitle || desc || '这里是分享朋友圈的内容', // 分享标题，由于分享到朋友圈没有desc，所以这里采用的策略是，指定的朋友圈分享标题优先，其次采用发送给朋友的描述，最后采用默认文案。
link: link || window.location.href, // 分享链接
imgUrl: imgUrl || 'https://dwz.cn/bQtHr9Iz', // 分享图标
success: function () {
if(step){
step = parseInt(step);
}
switch (step) {
case 0:
case 1:
step = 2;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 2:
step = 3;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 3:
step = 4;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 4:
step = 5;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
case 5:
step = 6;
window.location.href = 'http://www.baidu.com';
break;
default:
step = 1;
router.push({ path: '/mark5', query : {step : step, wxid: wxid}});
break;
}
}
})
})

Vue.wechat.error((res) => {

})
}


Vue.prototype.$wxShare = wxShare;




